___CSS_LOADER_EXPORT___.push([module.id, `/* Default styles (for desktop) */
body {
    font-size: 16px;
    background-color: #000000;
    margin: 0;
    padding: 0;
}

/* Tooltip styling */
.tooltip {
    position: absolute;
    background: linear-gradient(to bottom, #ff9999, #99ccff);
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 4px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
    font-family: Arial, sans-serif;
    font-size: 14px;
    color: #333;
    pointer-events: none;
    display: none;
}

/* Tooltip header */
.tooltip-header {
    font-weight: bold;
    margin-bottom: 8px;
    font-size: 16px;
}

/* Tooltip table */
.tooltip table {
    width: 100%;
    border-collapse: collapse;
}

.tooltip th, .tooltip td {
    background-color: white;
    padding: 4px 8px;
    text-align: left;
}

.tooltip th {
    background-color: #f5f5f5;
    font-weight: bold;
    border-bottom: 1px solid #ddd;
}

.tooltip td {
    border-bottom: 1px solid #eee;
}

.tooltip tr:last-child td {
    border-bottom: none;
}

/* Info pane styling */
.info-pane {
    font-size: 16px;
    color: #333;
    border-radius: 4px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
    background: linear-gradient(to bottom, #ff9999, #99ccff);
    padding: 10px;
    width: 300px;
    margin: 10px;
}

.info-pane-header {
    font-weight: bold;
    font-size: 18px;
    font-family: "Arial", sans-serif;
}

/* Info pane table */
.info-pane table {
    width: 100%;
    border-collapse: collapse;
    background: white;
}

.info-pane th, .info-pane td {
    padding: 4px 8px;
    text-align: left;
}

.info-pane th {
    background-color: #f5f5f5;
    font-weight: bold;
    border-bottom: 1px solid #ddd;
}

.info-pane td {
    border-bottom: 1px solid #eee;
}

.info-pane tr:last-child td {
    border-bottom: none;
}

/* Winning party styling */
.winner-republican {
    color: red;
    font-weight: bold;
}

.winner-democrat {
    color: blue;
    font-weight: bold;
}

/* Map container layout */
#map-container {
    display: flex;
    flex-direction: column; /* Stack state map above county map */
    align-items: flex-start; /* Align maps to the left */
    gap: 20px; /* Space between maps */
    margin-top: 20px; /* Optional space between chart and maps */
}

#state-map, #county-map {
    width: 600px; /* Fixed width for desktop */
    height: 450px; /* Fixed height for desktop */
}

#county-map {
    margin-top: 0; /* Remove extra margin for desktop */
}

/* Stacked bar chart styling */
#election-results-chart {
    width: 600px;
    text-align: center;
    margin: 0 auto 10px auto;
    transform: translateX(-350px);
}

/* Styling for bar chart text labels */
#election-results-chart text {
    font-size: 14px;
    fill: #f0f0f0;
    font-weight: bold;
}

/* Styling for bars in the stacked bar chart */
#election-results-chart rect {
    stroke: #333;
    stroke-width: 1px;
}

/* Styling for the 270-vote marker */
#election-results-chart line {
    stroke: black;
    stroke-width: 2px;
    stroke-dasharray: 4 2;
}

/* Styling for the 270-vote marker label */
#election-results-chart .marker-label {
    font-size: 12px;
    fill: black;
    font-weight: normal;
    position: absolute;
}

/* Popular vote container */
#popular-vote-container {
    color: #f0f0f0;
}

/* Update pane button styling */
.update-pane button {
    display: inline-block;
    margin-left: 5px;
    vertical-align: middle;
}

/* Zoom controls container */
.zoom-controls {
    position: absolute;
    top: 1500px;
    left: 20px;
    display: flex;
    gap: 10px;
}

/* Zoom button styling */
.zoom-button {
    padding: 10px;
    border: 1px solid #ccc;
    background-color: #f5f5f5;
    cursor: pointer;
}

.zoom-button:hover {
    background-color: #e0e0e0;
}

/* Text box styling */
#text-box {
    position: absolute;
    top: 60px;
    right: 10px;
    padding: 10px;
    color: orange;
    font-size: 16px;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 4px;
    max-width: 200px;
}

/* Navbar styling */
.navbar {
    background-color: #2e2e2e;
    margin-bottom: 50px;
    padding: 10px 0;
    top: 0;
    width: 100%;
    z-index: 1000;
    text-align: center;
}

.nav-menu {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    gap: 20px;
}

.nav-menu li {
    display: inline;
}

.nav-menu a {
    color: #ffffff;
    text-decoration: none;
    font-weight: bold;
    padding: 10px 15px;
}

.nav-menu a:hover {
    background-color: #444444;
    border-radius: 5px;
}

/* Mobile styles */
@media (max-width: 768px) {
    body {
        font-size: 14px;
    }

    #map-container {
        align-items: center; /* Center-align maps on mobile */
    }

    #state-map, #county-map {
        width: 100%; /* Full width for mobile */
        height: auto; /* Allow maps to scale */
    }

    #county-map {
        margin-top: 20px; /* Add space between maps on mobile */
    }

    #text-box {
        position: static;
        max-width: 100%;
        margin: 10px;
    }

    .navbar {
        padding: 5px 0;
    }

    .nav-menu {
        display: none;
        flex-direction: column;
        gap: 10px;
    }

    .nav-menu.active {
        display: flex;
    }

    .menu-icon {
        display: block;
        font-size: 24px;
        cursor: pointer;
    }

    .info-pane, .update-pane {
        width: 90%;
        margin: 10px auto;
    }

    .tooltip {
        font-size: 12px;
        padding: 5px;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,iCAAiC;AACjC;IACI,eAAe;IACf,yBAAyB;IACzB,SAAS;IACT,UAAU;AACd;;AAEA,oBAAoB;AACpB;IACI,kBAAkB;IAClB,wDAAwD;IACxD,sBAAsB;IACtB,aAAa;IACb,kBAAkB;IAClB,wCAAwC;IACxC,8BAA8B;IAC9B,eAAe;IACf,WAAW;IACX,oBAAoB;IACpB,aAAa;AACjB;;AAEA,mBAAmB;AACnB;IACI,iBAAiB;IACjB,kBAAkB;IAClB,eAAe;AACnB;;AAEA,kBAAkB;AAClB;IACI,WAAW;IACX,yBAAyB;AAC7B;;AAEA;IACI,uBAAuB;IACvB,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,yBAAyB;IACzB,iBAAiB;IACjB,6BAA6B;AACjC;;AAEA;IACI,6BAA6B;AACjC;;AAEA;IACI,mBAAmB;AACvB;;AAEA,sBAAsB;AACtB;IACI,eAAe;IACf,WAAW;IACX,kBAAkB;IAClB,wCAAwC;IACxC,wDAAwD;IACxD,aAAa;IACb,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,iBAAiB;IACjB,eAAe;IACf,gCAAgC;AACpC;;AAEA,oBAAoB;AACpB;IACI,WAAW;IACX,yBAAyB;IACzB,iBAAiB;AACrB;;AAEA;IACI,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,yBAAyB;IACzB,iBAAiB;IACjB,6BAA6B;AACjC;;AAEA;IACI,6BAA6B;AACjC;;AAEA;IACI,mBAAmB;AACvB;;AAEA,0BAA0B;AAC1B;IACI,UAAU;IACV,iBAAiB;AACrB;;AAEA;IACI,WAAW;IACX,iBAAiB;AACrB;;AAEA,yBAAyB;AACzB;IACI,aAAa;IACb,sBAAsB,EAAE,qCAAqC;IAC7D,uBAAuB,EAAE,2BAA2B;IACpD,SAAS,EAAE,uBAAuB;IAClC,gBAAgB,EAAE,0CAA0C;AAChE;;AAEA;IACI,YAAY,EAAE,4BAA4B;IAC1C,aAAa,EAAE,6BAA6B;AAChD;;AAEA;IACI,aAAa,EAAE,oCAAoC;AACvD;;AAEA,8BAA8B;AAC9B;IACI,YAAY;IACZ,kBAAkB;IAClB,wBAAwB;IACxB,6BAA6B;AACjC;;AAEA,sCAAsC;AACtC;IACI,eAAe;IACf,aAAa;IACb,iBAAiB;AACrB;;AAEA,8CAA8C;AAC9C;IACI,YAAY;IACZ,iBAAiB;AACrB;;AAEA,oCAAoC;AACpC;IACI,aAAa;IACb,iBAAiB;IACjB,qBAAqB;AACzB;;AAEA,0CAA0C;AAC1C;IACI,eAAe;IACf,WAAW;IACX,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA,2BAA2B;AAC3B;IACI,cAAc;AAClB;;AAEA,+BAA+B;AAC/B;IACI,qBAAqB;IACrB,gBAAgB;IAChB,sBAAsB;AAC1B;;AAEA,4BAA4B;AAC5B;IACI,kBAAkB;IAClB,WAAW;IACX,UAAU;IACV,aAAa;IACb,SAAS;AACb;;AAEA,wBAAwB;AACxB;IACI,aAAa;IACb,sBAAsB;IACtB,yBAAyB;IACzB,eAAe;AACnB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA,qBAAqB;AACrB;IACI,kBAAkB;IAClB,SAAS;IACT,WAAW;IACX,aAAa;IACb,aAAa;IACb,eAAe;IACf,oCAAoC;IACpC,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA,mBAAmB;AACnB;IACI,yBAAyB;IACzB,mBAAmB;IACnB,eAAe;IACf,MAAM;IACN,WAAW;